@import url("https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700&display=swap");
html {
  font-size: 10px;
}

* {
  margin: 0%;
  padding: 0;
  text-decoration: none;
  line-height: 1.5;
  box-sizing: border-box;
}

body {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  font-family: "Inter", sans-serif;
  color: white;
  padding: 4rem;
}

#root {
  width: 317px;
}

.header {
  border-radius: 10px 10px 0px 0px;
  background-color: #1a1b21;
}
.header img {
  width: 100%;
  border-radius: 10px 10px 0px 0px;
}

.main {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0.5rem 3rem;
  background-color: #1a1b21;
}
.main h1 {
  font-size: 2.5rem;
  text-align: center;
  line-height: 30px;
  font-weight: 700;
}
.main .front {
  font-size: 1.28rem;
  color: #f3bf99;
}
.main a {
  color: white;
}
.main div {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 1.3rem;
}
.main div button {
  width: 45%;
  height: 3.4rem;
  border-radius: 6px;
  font-size: 1.4rem;
}
.main div button i {
  margin-right: 1rem;
}
.main div button:nth-of-type(1) {
  background-color: white;
  color: black;
}
.main div button:nth-of-type(2) {
  background: #5093e2;
  color: white;
}
.main article h2 {
  font-size: 1.6rem;
  font-weight: 700;
}
.main article p {
  font-weight: 200;
  font-size: 1.1rem;
}
.main article:nth-of-type(1) {
  margin-bottom: 2.1rem;
  margin-top: 3.1rem;
}
.main article:nth-of-type(2) {
  margin-bottom: 3.1rem;
}

.footer {
  width: 100%;
  display: flex;
  justify-content: center;
  background: #161619;
  height: 6.4rem;
  border-radius: 0px 0px 10px 10px;
}
.footer section {
  width: 60%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 3.5rem;
  color: #918e9b;
}/*# sourceMappingURL=main.css.map */